@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  scroll-behavior: smooth;
}

html {
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .container {
    max-width: 1220px;
    width: 95%;
  }
}
@media (max-width: 1023px) {
  .container {
    width: 87.01%;
  }
}
@media (max-width: 767px) {
  .container {
    min-width: 270px;
    width: 84.37%;
  }
}

.button {
  padding: 11px 16px;
  border-radius: 3px;
  color: #f2f5f6;
  background-color: transparent;
  border: 1px solid #f2f5f6;
  text-transform: uppercase;
  cursor: pointer;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  font-style: normal;
  line-height: 16px;
  color: #f2f5f6;
  letter-spacing: 0.05em;
}
.button:focus-visible {
  outline: 1px solid #00b2ff;
  outline-offset: 2px;
}
.button:hover {
  opacity: 0.8;
}
.button:active {
  opacity: 0.5;
}

.header {
  position: fixed;
  width: 100%;
  padding: 25px 0 35px;
  background-image: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}
@media (max-width: 1023px) {
  .header {
    padding: 10px 0 30px;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 5px 0 25px;
  }
}

.header__container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.header__logo {
  margin-right: auto;
}

.header__menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  margin-left: 7px;
  padding: 0;
  width: 474px;
  list-style: none;
}
@media (max-width: 1023px) {
  .header__menu {
    margin-left: auto;
    width: auto;
  }
}
@media (max-width: 767px) {
  .header__menu {
    margin-left: auto;
    width: auto;
  }
}

.header__link {
  display: flex;
  align-items: center;
  width: 100%;
  position: relative;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 18px;
  color: #f2f5f6;
  padding: 10px 0 10px 35px;
}
.header__link::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  background-position: center;
}
.header__link--mail {
  margin-right: 122px;
}
.header__link--mail::before {
  background-image: url("../img/svg/email-icon.svg");
}
.header__link--tel {
  margin-right: 164px;
}
.header__link--tel::before {
  background-image: url("../img/svg/phone-call-icon.svg");
}
.header__link:focus-visible {
  outline: 1px solid #00b2ff;
  outline-offset: 2px;
}
.header__link:hover {
  opacity: 0.8;
}
.header__link:active {
  opacity: 0.5;
}
@media (max-width: 1023px) {
  .header__link {
    width: 40px;
    height: 40px;
    margin-right: 20px;
  }
  .header__link::before {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
@media (max-width: 767px) {
  .header__link--mail {
    margin-right: 20px;
  }
  .header__link--tel {
    margin-right: 10px;
  }
}

@media (max-width: 1023px) {
  .header__link-text {
    display: none;
  }
}
@media (max-width: 767px) {
  .header__link-text {
    display: none;
  }
}

.header__button {
  margin-right: 61px;
  margin-left: auto;
}
@media (max-width: 1023px) {
  .header__button {
    display: none;
  }
}
@media (max-width: 767px) {
  .header__button {
    display: none;
  }
}

.logo a {
  display: block;
}

.logo img {
  margin-top: 5px;
}
@media (max-width: 1023px) {
  .logo img {
    margin-top: 10px;
  }
}
@media (max-width: 767px) {
  .logo img {
    width: 35px;
    height: 35px;
  }
}

.main__intro {
  margin-bottom: 120px;
}
@media (max-width: 1023px) {
  .main__intro {
    margin-bottom: 80px;
  }
}
@media (max-width: 767px) {
  .main__intro {
    margin-bottom: 50px;
  }
}

.main__advantages {
  margin-bottom: 120px;
}
@media (max-width: 1023px) {
  .main__advantages {
    margin-bottom: 80px;
  }
}
@media (max-width: 767px) {
  .main__advantages {
    margin-bottom: 50px;
  }
}

.intro {
  padding-top: 303px;
  padding-bottom: 280px;
  background-color: #041827;
  background-image: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url("../img/background/intro-bg.jpg");
  background-size: 1920px 880px;
  background-repeat: no-repeat;
}
@media (min-resolution: 2dppx) {
  .intro {
    background-image: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url("../img/background/intro-bg@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .intro {
    padding-top: 178px;
    padding-bottom: 205px;
    background-size: 1396px 640px;
    background-position: 20% 0%;
  }
}
@media (max-width: 767px) {
  .intro {
    padding-top: 150px;
    padding-bottom: 190px;
    background-size: cover;
    background-position: 26% 0%;
  }
}

.intro__content {
  margin-left: 10.24%;
}
@media (max-width: 1023px) {
  .intro__content {
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .intro__content {
    margin-left: 0;
  }
}

.intro h1 {
  margin: 0;
  margin-bottom: 33px;
  text-transform: uppercase;
  letter-spacing: 0.07em;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 80px;
  font-weight: 700;
  font-style: normal;
  line-height: 94px;
  color: #f2f5f6;
}
@media (max-width: 1023px) {
  .intro h1 {
    margin-bottom: 2px;
    font-size: 64px;
    line-height: 75px;
  }
}
@media (max-width: 767px) {
  .intro h1 {
    margin-bottom: 8px;
    font-size: 36px;
    line-height: 42px;
  }
}

.intro p {
  margin: 0;
  margin-bottom: 48px;
  width: 56.14%;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 22px;
  font-weight: 400;
  font-style: normal;
  line-height: 34px;
  color: #f2f5f6;
}
@media (max-width: 1023px) {
  .intro p {
    width: 100%;
    margin-bottom: 57px;
  }
}
@media (max-width: 767px) {
  .intro p {
    width: 100%;
    margin-bottom: 44px;
    font-size: 14px;
    line-height: 22px;
  }
}

.intro__button {
  display: inline-block;
  padding: 17px 27px;
  font-size: 18px;
  line-height: 21px;
  background-color: #00b2ff;
  border: none;
}
@media (max-width: 1023px) {
  .intro__button {
    margin-left: 45px;
  }
}
@media (max-width: 767px) {
  .intro__button {
    font-size: 14px;
    line-height: 16px;
    width: 100%;
    margin: 0;
    padding: 12px 30px;
    text-align: center;
  }
}

.intro__button-text--tel {
  display: none;
}
@media (max-width: 767px) {
  .intro__button-text {
    display: none;
  }
  .intro__button-text--tel {
    display: inline-block;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 32px;
  row-gap: 30px;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .advantages__list {
    grid-template-columns: 1fr;
    column-gap: 0;
  }
}

.advantages__item {
  padding: 40px 45px 35px;
  border: 1px solid #ebebeb;
  border-left: 5px solid #00b2ff;
}
.advantages__item:hover {
  background-color: #00b2ff;
  border-color: transparent;
}
@media (max-width: 767px) {
  .advantages__item {
    padding: 30px 30px 35px;
  }
}

.advantages__item svg {
  fill: #00b2ff;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  .advantages__item svg {
    width: 30px;
    height: 30px;
  }
}

.advantages__item:hover svg {
  fill: #f2f5f6;
}

.advantages__item h3 {
  margin: 0;
  margin-bottom: 15px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  font-style: normal;
  line-height: 34px;
  color: #00b2ff;
}
@media (max-width: 767px) {
  .advantages__item h3 {
    font-size: 20px;
    line-height: 28px;
  }
}

.advantages__item p {
  margin: 0;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 22px;
  color: #041827;
}
@media (max-width: 767px) {
  .advantages__item p {
    font-size: 14px;
    line-height: 19px;
  }
}

.advantages__item:hover h3,
.advantages__item:hover p {
  color: #f2f5f6;
}

